@import url(https://fonts.googleapis.com/css?family=Montserrat|PT+Serif);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset utf-8;

/* 웹폰트 CSS */
 
/* CSS 초기화 */
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video{
margin:0;
padding:0;
border:0;
outline:0;
font-size:100%;
vertical-align:baseline;
background:transparent;
}

body{
font-family:'Montserrat',DroidSans,AppleSDGothicNeo,Sans-serif,Helvetica;
line-height:1;
}

article,aside,details,figcaption,figure, footer,header,hgroup,menu,nav,section{
display:block;
}

nav ul, li{
list-style:none;
}

a{
margin:0;
padding:0;
font-size:100%;
vertical-align:baseline;
background:transparent;
text-decoration:none;
}

img{
vertical-align:top;
}

hr{
display:block;
height:2px;
border:0;
margin:0;
padding:0;
}

input, textarea{
width:100%;
margin:0;
padding:0;
border:0;
border-bottom:1px solid #757575;
color:#3f51b5;
font-family:'Montserrat';
font-size:12px;
vertical-align:top;
border-radius:0;
box-sizing:content-box;
-webkit-appearance:none;
   -moz-appearance:none;
        appearance:none;
}

::-moz-input-placeholder, ::-moz-textarea-placeholder{
color:#3f51b5;
}

::-webkit-input-placeholder, ::-webkit-textarea-placeholder{
color:#3f51b5;
}    
.header{position:fixed;padding:16px 5%;z-index:1000;width:100%;background-color:rgba(255,255,255,0.99);box-shadow:0 2px 2px #efefef}.header .gnb{position:absolute;top:100%;left:0;width:100%;background:#3f51b5;z-index:1;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out}.header .gnb li{border-bottom:1px solid #303f9f;display:block;padding:14px 0 14px 5%;font-size:14px;color:#fff;cursor:pointer}.header .menu-toggle-btn{float:right;width:25px;margin-top:4px;cursor:pointer}.header .menu-toggle-btn span{display:block;width:100%;height:3px;background:#3f51b5}.header .menu-toggle-btn span:nth-child(2){margin:5px 0}@media all and (min-width: 768px){.header{padding:20px 52px}.header .gnb{display:block !important;position:static;float:right;width:auto;margin-top:6px;background:none}.header .gnb li{float:left;margin-left:21px;border-bottom:0;color:#3f51b5;padding:0;font-size:16px}.header .gnb li:hover{font-weight:bold}.header .menu-toggle-btn{display:none !important}}

.section-visual{background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.3)), to(rgba(0,0,0,0.3))),url(/portfolio/static/media/visual.7be55138.jpg);background-image:linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)),url(/portfolio/static/media/visual.7be55138.jpg);background-repeat:no-repeat;background-position:bottom left;background-size:cover;position:relative}.section-visual .container{padding:160px 0;color:#fff}.section-visual img{display:block;width:100%;max-width:100%;height:auto;background-size:cover}.section-visual h1{font-size:42px;font-weight:bold;margin-bottom:40px}.section-visual span{font-size:30px}#cursor{border-left:0.1em solid;-webkit-animation:blink 0.7s steps(1) infinite;animation:blink 0.7s steps(1) infinite}@-webkit-keyframes blink{50%{border-color:transparent}}@keyframes blink{50%{border-color:transparent}}

.project-section{margin-top:55px}.project-section .sec-tit{margin-bottom:47px;text-align:center}.project-section .project-list li{width:100%;margin-top:45px}.project-section .project-list li:first-child{margin-top:0}.project-section .project-list li img{display:block;width:90%;max-width:90%;margin:0 auto}.project-title{width:100%;font-size:25px;font-weight:500;text-align:left;letter-spacing:0.05em;margin:20px 0}.project-title strong{color:#3f51b5;margin-right:13px;font-size:26px}.project-desc{width:100%;font-size:1em;font-weight:300;line-height:1.5em}.project-tag{margin:20px 0;display:flex;flex-wrap:wrap}.project-tag span{color:#9e9e9e;margin-left:7px}.project-tag span:first-child{margin-left:0}.project-btn{display:flex;justify-content:space-between;width:100%;height:50px}.project-btn span{width:48%;background:#fff;border:3px solid #3f51b5;cursor:pointer}.project-btn span a{display:flex;justify-content:center;align-items:center;width:100%;height:100%;color:#3f51b5}.project-btn span a:hover{background:#3f51b5;color:#fff}@media all and (min-width: 1132px){.project-section{margin-bottom:80px;margin-top:77px}.project-section .sec-tit{float:left}.project-section .project-list li{display:flex}.project-section .project-list li img{display:block;width:50%}}

.skill-section{margin-top:77px}.skill-section .sec-tit{margin-bottom:47px;text-align:center}.skill-section .skill-list{display:flex;padding:30px 12.5%;flex-wrap:wrap}.skill-section .skill-list li{width:29.16666666666667%;margin-left:6.25%;text-align:center}.skill-section .skill-list li:nth-last-child(3n){margin-left:0}.skill-section .skill-list li img{height:52px}.skill-section .skill-list li h3{margin:25px 0 35px 0;color:#3f51b5;font-weight:normal}@media all and (min-width: 768px){.skill-section{margin-top:124px}.skill-section .sec-tit{margin-bottom:76px}.skill-section .skill-list li{width:31.59722222222222%;margin-left:2.604166666666667%;margin-top:0}}@media all and (min-width: 1132px){.skill-section .sec-tit{float:left;margin-bottom:0}.skill-section .skill-list{float:right;width:74.5583038869258%}.skill-section .skill-list li{width:14.69194312796209%;margin-left:2.369668246445498%}}

.footer{margin-top:77px;border-top:3px dashed #3f51b5}.footer .container{width:100%;display:flex;flex-direction:column;padding:20px 60px;box-sizing:border-box}.footer-content1{max-width:100%;display:flex;justify-content:center;align-items:center;margin-bottom:10px}.footer-content1 img{width:35px;height:35px;margin-right:7px}.footer-content1 span{font-size:20px;letter-spacing:-1px}.footer-content2{width:100%;display:flex;justify-content:center;align-items:center}.footer-content2 a{cursor:pointer}.footer-content2 a:first-child{margin-right:25px}.footer-content2 a img{width:50px;height:50px}@media all and (min-width: 768px){.footer .container{padding:30px 100px;flex-direction:row}.footer-content1{justify-content:flex-start}.footer-content2{justify-content:flex-end}}@media all and (min-width: 1132px){.footer .container{padding:40px 150px}}

.about-section{margin-top:59px;text-align:center}.about-section .sec-tit{margin-bottom:30px}.about-section .desc{font-family:"PT Serif";color:#616161;line-height:1.9}@media all and (min-width: 768px){.about-section{margin-top:95px}}@media all and (min-width: 1132px){.about-section{text-align:left}.about-section .sec-tit{float:left;margin-bottom:0}.about-section .desc{position:relative;float:right;padding-left:20px;font-family:"PT Serif";color:#616161;line-height:1.9}.about-section .desc:before{display:block;position:absolute;top:9px;left:0;width:2px;height:44px;background:#e0e0e0;content:""}}

*{box-sizing:border-box}.container{width:90%;max-width:1132px;margin:0 auto;position:relative}.cfixed:after,.container:after{display:block;content:"";clear:both}.sec-tit{font-size:42px;color:#3f51b5;font-weight:normal}

